import React from "react";
import marked from "marked";
import { Link } from "react-router-dom";

export default class extends React.Component {
  state = { markdown: "" };

  componentDidMount() {
    const textpath = require("../blog/" + this.props.match.params.title);
    fetch(textpath)
      .then(res => res.text())
      .then(text => this.setState({ markdown: marked(text) }))
  }

  render() {
    return (
      <React.Fragment>
        <Link to="/">home</Link>
        <section>
          <article className="markdown-body" dangerouslySetInnerHTML={{__html: this.state.markdown}}></article>
        </section>
        <Link to="/">home</Link>
      </React.Fragment>
    )
  }
}